export default function urnaEletronica() {
  //declaração das Variáveis que serão usadas abaixo:
  this.contador = document.querySelector(".contador")
  this.digito = document.querySelector(".digitos-tela")
  this.digitos = document.querySelectorAll(".digitos-tela")
  this.pisca = document.querySelector(".pisca")
  this.telaUrna = document.querySelector(".tela-urna")
  this.infosUrna = document.querySelectorAll(".tela-urna")
  this.candidatosInfo = document.querySelector(".candidato-info")
  this.campoFoto = document.querySelector(".campo-foto")
  this.nomeCandidato = document.querySelector(".nome")
  this.partidoCandidato = document.querySelector(".partido")
  this.numeros = document.querySelector(".numeros")

  this.confirma = document.querySelector(".confirma")
  this.corrige = document.querySelector(".corrige")
  this.branco = document.querySelector(".branco")

  this.votoNulo = document.querySelector(".voto-nulo")
  this.votoBranco = document.querySelector(".voto-branco")

  console.log(this.telaUrna.children)

  this.votosValor = ''
  this.votos = []
  this.VotosChecados = this.votosValor
  this.votosValido = false

  this.inicia = () => {
    this.capturaCliques()
  };

  this.handleClick = event => {
    const el = event.target;

    if(el.classList.contains("numeros")) this.adicionaTela(el)
    if(el.classList.contains("corrige")) this.corrigeVoto()
    if(el.classList.contains("branco")) this.adicionaBranco()
  };

  this.capturaCliques = () => {
    this.handleClick
    document.addEventListener("click", this.handleClick)
  };

  this.tiraDigitosTela = () => {
    this.digitos.forEach((digito) => {
      digito.classList.add("esconde-pisca")
    })
  }

  this.criaCandidato = (foto, nome, partido) => {
    this.campoFoto.setAttribute("src", foto)
    this.nomeCandidato.innerHTML("nome :" + nome)
    this.partidoCandidato.innerHTML("partido :" + partido)
  }

  this.estadoInicial = () => {
    this.telaUrna.innerHTML = `
        <div class="digitos-tela pisca"></div>
        <div class="digitos-tela"></div>
        <div class="digitos-tela"></div>
        <div class="digitos-tela"></div>
        <div class="digitos-tela"></div>

        <div class="voto-branco pisca">
            <p class="voto-branco-texto">VOTO BRANCO</p>
        </div>
        <div class="voto-nulo pisca">
            <p class="voto-nulo-texto">VOTO NULO</p>
        </div>
        <div class="candidato-info">
            <div class="campo-foto">
                <img class="foto" src="../src/img/Goku.png" alt="Foto de Goku">
            </div>
            <div class="nome">Son Goku</div>
            <div class="partido">Dragon Ball Z</div>
        </div>
    `;
    this.votosValor = ''
    this.votos = []
    this.pisca = document.querySelector(".pisca")
  }

  /*this.adicionaInfosTela = () => {
    this.candidatosInfo
  }*/

  this.corrigeVoto = () => {
    this.digitos.forEach((digito) => {
      digito.innerHTML = ''
      digito.classList.remove('pisca')
      this.votos = []
    })
    this.digito.classList.add('pisca')
    this.pisca = document.querySelector(".pisca")
    this.estadoInicial()
  }

  this.adicionaBranco = () => {
    this.tiraDigitosTela()
    this.votoBranco.classList.remove("voto-branco")
    this.votoBranco.classList.add("aparece-branco")
    this.votoNulo.classList.add("voto-nulo")
  }

  this.adicionaTela = (el) => {
    /*let somNumeros = new Audio();
    somNumeros.src = "audios/numeros.mp3";
    somNumeros.play();*/
    if (this.pisca){
      this.pisca.innerText += el.innerText;
      this.pisca.classList.remove("pisca");
      this.pisca.nextElementSibling.classList.add("pisca");
      this.pisca = document.querySelector(".pisca")
      this.votos.push(el.innerHTML);

      if(this.votos.length === 5){
        this.checaVoto();
      }
    }
  }

    this.checaVoto = () => {
      this.votosValor = this.votos.reduce((ac, val) => String(ac) + String(val), '');
      this.candidatoOuNulo(this.votosValor);
      console.log(this.votosValor);
    }

  this.candidatoOuNulo = (votos) => {
    switch (votos) {
      case '55555':
        this.tiraDigitosTela()
        this.adicionaInfosTela()
        this.criaCandidato(
        "../img/Goku.png",
        "Son Goku",
        "Dragon Ball Z"
        )
        break
      default:
        this.tiraDigitosTela()
        this.votoNulo.classList.remove("voto-nulo")
        this.votoNulo.classList.add("aparece-nulo")
    }
  }

}
const urna = new urnaEletronica()
urna.inicia()

